@import "https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: #161616;
  justify-content: center;
  align-items: center;
  display: flex;
}

.coin {
  width: 460px;
  height: 80px;
  border-radius: 8px;
  margin: 38px 0;
  font-family: Mukta, sans-serif;
  display: flex;
}

.coin .coin-logo {
  width: 80px;
  height: 100%;
  align-items: center;
  justify-items: center;
  display: grid;
}

.coin .coin-logo img {
  width: 70%;
  height: 70%;
}

.coin .coin-name {
  height: 100%;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.coin .coin-name h3 {
  color: #fff;
  text-transform: uppercase;
  font-size: 26px;
}

.coin .coin-name span {
  color: #aaa;
  margin: 6px 0 0 2px;
  font-size: 14px;
  font-weight: 500;
}

.coin .coin-price {
  text-align: right;
  align-items: center;
  margin-left: auto;
  padding: 8px 22px 0 0;
  display: grid;
}

.coin .coin-price .price {
  margin-top: -6px;
  font-size: 26px;
  font-weight: 700;
}

.coin .coin-price .change {
  color: #aaa;
  font-style: 16px;
  margin-top: -30px;
  font-weight: 600;
}

.coin.rising {
  background: linear-gradient(90deg, #202022, rgba(78, 182, 69, .063));
}

.coin.rising .coin-price .price {
  color: #30ff20;
}

.coin.falling {
  background: linear-gradient(90deg, #202022, rgba(170, 68, 68, .145));
}

.coin.falling .coin-price .price {
  color: #ff4040;
}

